
import {
  collection,
  doc,
  getDoc,
  getDocs,
  addDoc,
  updateDoc,
  deleteDoc,
  query,
  where,
  orderBy,
  limit as firestoreLimit,
  Timestamp,
} from "firebase/firestore";
import { db } from "@/lib/firebase/init";
import type { Product, ProductStatus } from "@/lib/types";

export interface CreateProductInput {
  name: string;
  sku: string;
  categoryId: string;
  vendorId: string;
  description: string;
  price: number;
  currency?: string;
  taxPercent?: number;
  discountPercent?: number;
  status?: ProductStatus;
  images?: string[];
  unit?: string;
  minOrderQty?: number;
  createdBy?: string;
  // Inventory related
  stock: number;
  reorderLevel: number;
}

export interface UpdateProductInput {
  name?: string;
  sku?: string;
  categoryId?: string;
  vendorId?: string;
  description?: string;
  price?: number;
  currency?: string;
  taxPercent?: number;
  discountPercent?: number;
  status?: ProductStatus;
  images?: string[];
  unit?: string;
  minOrderQty?: number;
  // Inventory
  stock?: number;
  reorderLevel?: number;
}


export class ProductService {
  private collectionRef = db ? collection(db, "products") : null;
  // In a real app, you would also interact with 'inventory', 'productCategories', etc.

  // Get all products with optional filters
  async getAll(options?: {
    limit?: number;
    category?: string; // Should be categoryId
    activeOnly?: boolean;
  }) {
    if (!this.collectionRef) {
        return { success: false, error: "Firebase not initialized." };
    }
    try {
      const constraints = [];

      // Note: The original spec used 'activeOnly' but the new status model is more complex.
      // For simplicity, we'll treat 'ACTIVE' as the only visible status for now.
      if (options?.activeOnly !== false) {
        constraints.push(where("status", "==", "ACTIVE"));
      }
      
      // The `category` filter would now join on productCategories which is more complex.
      // We will simulate this on the client or simplify it here for now.
      // If we had denormalized `categoryName` on product, it would be:
      // if (options?.category && options.category !== 'all') {
      //   constraints.push(where("categoryName", "==", options.category));
      // }

      constraints.push(orderBy("createdAt", "desc"));

      if (options?.limit) {
        constraints.push(firestoreLimit(options.limit));
      }

      const q = query(this.collectionRef, ...constraints);
      const snapshot = await getDocs(q);

      const products: Product[] = [];
      snapshot.forEach((doc) => {
        const data = doc.data();
        products.push({
          id: doc.id,
          name: data.name || "",
          description: data.description || "",
          price: data.price || 0,
          sku: data.sku || "",
          categoryId: data.categoryId || "",
          vendorId: data.vendorId || "",
          currency: data.currency || "SAR",
          taxPercent: data.taxPercent || 0,
          discountPercent: data.discountPercent || 0,
          status: data.status || "DRAFT",
          unit: data.unit || "unit",
          minOrderQty: data.minOrderQty || 1,
          images: data.images || [],
          createdBy: data.createdBy || "",
          stock: data.stock || 0, // This would ideally come from a join with 'inventory'
          reorderLevel: data.reorderLevel || 10,
          categoryName: data.categoryId, // Denormalized for display
          createdAt: data.createdAt || Timestamp.now(),
          updatedAt: data.updatedAt || Timestamp.now(),
        });
      });

      return {
        success: true,
        data: products,
        count: products.length,
      };
    } catch (error) {
      console.error("Error in getAll:", error);
      return {
        success: false,
        error: "Failed to fetch products",
        details: error instanceof Error ? error.message : "Unknown error",
      };
    }
  }

  // Get product by ID
  async getById(id: string) {
    if (!db) return { success: false, error: "Firebase not initialized." };
    
    try {
      const docRef = doc(db, "products", id);
      const docSnap = await getDoc(docRef);

      if (!docSnap.exists()) {
        return { success: false, error: "Product not found" };
      }

      const data = docSnap.data();
      const product: Product = {
        id: docSnap.id,
        name: data.name || "",
        description: data.description || "",
        price: data.price || 0,
        sku: data.sku || "",
        categoryId: data.categoryId || "",
        vendorId: data.vendorId || "",
        currency: data.currency || "SAR",
        taxPercent: data.taxPercent || 0,
        discountPercent: data.discountPercent || 0,
        status: data.status || "DRAFT",
        unit: data.unit || "unit",
        minOrderQty: data.minOrderQty || 1,
        images: data.images || [],
        createdBy: data.createdBy || "",
        stock: data.stock || 0,
        reorderLevel: data.reorderLevel || 10,
        categoryName: data.categoryId,
        createdAt: data.createdAt || Timestamp.now(),
        updatedAt: data.updatedAt || Timestamp.now(),
      };

      return { success: true, data: product };
    } catch (error) {
      console.error("Error in getById:", error);
      return { success: false, error: "Failed to fetch product" };
    }
  }

  // Create new product
  async create(productData: CreateProductInput) {
    if (!this.collectionRef) return { success: false, error: "Firebase not initialized." };
    
    try {
      const now = Timestamp.now();
      const productToCreate = {
        name: productData.name,
        sku: productData.sku,
        categoryId: productData.categoryId,
        vendorId: productData.vendorId,
        description: productData.description,
        price: productData.price,
        currency: productData.currency || "SAR",
        taxPercent: productData.taxPercent || 15,
        discountPercent: productData.discountPercent || 0,
        status: productData.status || "DRAFT",
        images: productData.images || [],
        unit: productData.unit || "Nos",
        minOrderQty: productData.minOrderQty || 1,
        createdAt: now,
        updatedAt: now,
        createdBy: productData.createdBy || "admin_uid", // Should get current user
        // Inventory fields (denormalized for simplicity, but should be in `inventory` collection)
        stock: productData.stock || 0,
        reorderLevel: productData.reorderLevel || 10,
      };

      const docRef = await addDoc(this.collectionRef, productToCreate);
      return { success: true, data: { id: docRef.id, ...productToCreate }, message: "Product created successfully" };
    } catch (error) {
      console.error("Error in create:", error);
      return { success: false, error: "Failed to create product" };
    }
  }

  // Update existing product
  async update(id: string, updates: UpdateProductInput) {
    if (!db) return { success: false, error: "Firebase not initialized." };
    
    try {
      const docRef = doc(db, "products", id);
      const docSnap = await getDoc(docRef);

      if (!docSnap.exists()) {
        return { success: false, error: "Product not found" };
      }

      const updateData: any = { ...updates, updatedAt: Timestamp.now() };

      await updateDoc(docRef, updateData);
      
      const updatedDoc = await getDoc(docRef);
      return { success: true, data: { id: updatedDoc.id, ...updatedDoc.data() }, message: "Product updated successfully" };
    } catch (error) {
      console.error("Error in update:", error);
      return { success: false, error: "Failed to update product" };
    }
  }

  // Delete product
  async delete(id: string) {
    if (!db) return { success: false, error: "Firebase not initialized." };
    try {
      const docRef = doc(db, "products", id);
      await deleteDoc(docRef);
      return { success: true, message: "Product deleted successfully" };
    } catch (error) {
      console.error("Error in delete:", error);
      return { success: false, error: "Failed to delete product" };
    }
  }
}

// Export singleton instance
export const productService = new ProductService();

    